button{
    width: 200px;
    padding: 0 1.1rem;
}
i{
    text-align: center;
    padding: 0 1.1rem;
}
#new-quote:hover{
    transform: scale(1.1);
}
#new-quote:active{
    outline: none;
}
#new-quote:focus{
    outline: none;
}
@media(orientation: landscape){
    #quote-div{
        width: 430px;
    }
}

@media(orientation: portrait){
    #quote-div{
        width: 75vw;
    }

    h2{
        font-size: 50px;
    }
    h3{
        font-size: 50px;
    }

    i{
        width: 100px;
        font-size: 4rem;
    }
    button{
        font-size: 50px;
    }
    
}